    <ul class="my-order-tab">
      <li @click="functionClick">
        <img src="../../assets/movedetail.png" alt="" />
        <div class="common-p">电影订单</div>
      </li>
      <li @click="functionClick">
        <img src="../../assets/goodsdetail.png" alt="" />
