      </li>
    </ul>
    <!-- <div class="imgurl">
      <van-uploader :after-read="afterRead" />
      <img :src="imgUrl" v-if="imgUrl" alt="" />
    </div> -->

    <!-- <div>
      <input
        type="file"
        name="file"
        value="上传头像"
        @change="handleToUpload"
      />
      <img class="userHead" :src="$store.state.user.userHead" />
