        <span class="label"></span>
        <img class="arrow" src="../../assets/arrowright.png" alt="" /></div
    ></router-link>
    <div class="margin-set" @click="outLogin">
      <span class="label">退出登录</span>
      <img class="arrow" src="../../assets/arrowright.png" alt="" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
const coupon = {
  available: 1,
  condition: "无使用门槛\n最多优惠12元",
  reason: "",
  value: 990,
  name: "影城优惠卷",
  startAt: 1489104000,
  endAt: 1514592000,
  valueDesc: "9.9",
  unitDesc: "元",
};
export default {
  name: "center",
  data() {
    return {
      //  fileList: [
      //         { url: '',isImage: true  },
      //     ],

      imgUrl: "",
      chosenCoupon: -1,
      coupons: [coupon],
      disabledCoupons: [coupon],
      showList: false,
    };
  },
  created() {},
  mounted() {
    // console.log(this.$store.state.user.userHead);
    //  this.fileList[0].url=this.$store.state.user.userHead;
  },
  components: {},
  methods: {
    // 优惠卷
    onChange(index) {
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange(code) {
      this.coupons.push(coupon);
      console.log(code);
    },

    //   文件上传
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      console.log(file);
      this.imgUrl = file.content;
    },
    // 功能未开放
    functionClick() {
      //   if (this.$store.state.userHead) {
      //     this.$toast("此功能暂未开放");
      //   } else {
      //     this.$router.push("/login");
      //   }
      this.$toast("此功能暂未开放");
    },
    
    // 退出登录
    outLogin() {
      // this.axios.get("/api2/users/logout").then((res) => {
      //   var status = res.data.status;
      //   if (status === 0) {
      //     localStorage.removeItem("name");
      //     localStorage.removeItem("isAdmin");
      //     this.$store.commit("user/USER_NAME", {
      //       name: "",
      //       isAdmin: false,
      //       userHead: "",
      //     });
      //     this.$router.push("/mine/login");
      //   }
      // });
      this.$dialog
        .confirm({
          title: "退出账号确认",
          message: "您是否想要退出当前账号？",
          confirmButtonColor: "#ff5f16",
        })
        .then(() => {
          // 改变当前登陆状态
          this.$store.commit("changeLogin", false);
          this.$toast("退出成功");
          // 刷新当前页面
          this.$router.push("/mine/login");
        })
        .catch((err) => err);
    },
    /* afterRead(file) {
        // 此时可以自行将文件上传至服务器
        console.log(file.file);
        var param = new FormData()
        param.append('file',file.file,file.file.name)
        var config = {
            headers:{
                'Content-Type':'multipart/form-data'
            }
        }

        this.axios.post('/api2/users/uploadUserHead',param,config).then((res)=>{
            var status = res.data.status;
            if ( status === 0) {
                Dialog.alert({
                closeOnClickOverlay:true,
                title: '头像上传成功',
                // message: res.data.msg,
                }).then(() => {
                // on close
                    this.$store.commit('user/USER_NAME',{ 
                        userHead : res.data.data.userHead
                    });
                });
            }else{
                Dialog.alert({
                closeOnClickOverlay:true,
                title: '头像上传失败',
                // message: res.data.msg,
                }).then(() => {
                // on close
                });
            }
        })
        }, */
    //------------------------------------------------------
    handleToUpload(ev) {
      var file = ev.target.files[0];
      var param = new FormData();
      param.append("file", file, file.name);
      var config = {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      };

      axios({
        method: "post",
        url: "/api2/users/uploadUserHead",
        data: {
          param,
          config,
        },
      })
        // this.axios.post('/api2/users/uploadUserHead',param , config)
        .then((res) => {
          console.log("222");
          var status = res.data.status;
          var This = this;
          console.log(status);
          if (status === 0) {
            Dialog.alert({
              closeOnClickOverlay: true,
              title: "头像上传成功",
              // message: res.data.msg,
            }).then(() => {
              // on close
              This.$store.commit("user/USER_NAME", {
                name: This.$store.state.user.name,
                isAdmin: This.$store.state.user.isAdmin,
                userHead: res.data.data.userHead + "?" + Math.random(),
              });
            });
          } else {
            Dialog.alert({
              closeOnClickOverlay: true,
              title: "头像上传失败",
              // message: res.data.msg,
            }).then(() => {
              // on close
            });
          }
        });
    },
  },

  beforeRouteEnter(to, from, next) {
    axios.get("/api2/users/getUser").then((res) => {
      var status = res.data.status;
      if (status === 0) {
        next((vm) => {
          localStorage.setItem("name", res.data.data.username);
          localStorage.setItem("isAdmin", res.data.data.isAdmin);
          vm.$store.commit("user/USER_NAME", {
            name: res.data.data.username,
            isAdmin: res.data.data.isAdmin,
            userHead: res.data.data.userHead,
          });
        });
      } else {
        next("/mine/login");
      }
    });
  },
};
</script>

